import { Component, h, Prop, State } from '@stencil/core';
import { OpenDirection } from '../menu/menu.types';
import { Link } from '@limetech/lime-elements';
// import { markdownToHTML } from './markdown-parser';

/**
 * Help component renders an impermanent layer that is displayed on top of other content.
 * @exampleComponent limel-example-help
 * @exampleComponent limel-example-read-more
 * @exampleComponent limel-example-open-direction
 */
@Component({
    tag: 'limel-help',
    shadow: true,
    styleUrl: 'help.scss',
})
export class Help {
    /**
     * The markdown content that will be displayed in the popover.
     */
    @Prop()
    public value: string;

    /**
     * Visualizes the trigger element. Defaults to **?**
     * :::important
     * Be consistent across the product if you want to change it to a custom text.
     * :::
     */
    @Prop()
    public trigger: string = '?';

    /**
     * If supplied, it will render a "Read more" link at the bottom of the content.
     */
    @Prop()
    public readMoreLink?: Link;

    /**
     * Decides the popover's location in relation to the trigger. Defaults to `top`.
     */
    @Prop({ reflect: true })
    public openDirection: OpenDirection = 'top';

    @State()
    private isOpen = false;

    // private rootElement: HTMLDivElement;

    // @Watch('text')
    // public async textChanged() {
    //     const html = await markdownToHTML(this.text, {
    //         forceHardLineBreaks: true,
    //     });
    //     this.rootElement.innerHTML = html;
    // }

    public async componentDidLoad() {
        // this.textChanged();
    }

    public render() {
        return [
            <limel-popover
                open={this.isOpen}
                onClose={this.onPopoverClose}
                openDirection={this.openDirection}
            >
                <button slot="trigger" onClick={this.openPopover}>
                    {this.trigger}
                </button>
                <limel-markdown-viewer
                    style={{
                        margin: '0.5rem 1rem',
                        'max-height': '30rem',
                        overflow: 'hidden auto',
                    }}
                    content={this.value}
                />
                {this.renderReadMoreLink()}
            </limel-popover>,
        ];
    }

    private renderReadMoreLink = () => {
        if (!this.readMoreLink) {
            return;
        }

        return (
            <a
                style={{
                    color: 'rgb(var(--color-sky-default))',
                    'text-decoration': 'none',
                    margin: '0.5rem 1rem',
                }}
                href={this.readMoreLink?.href}
                target={this.readMoreLink?.target}
                title={this.readMoreLink?.title}
                tabindex="0"
            >
                {this.readMoreLink?.text}
            </a>
        );
    };

    private openPopover = (event: MouseEvent) => {
        event.stopPropagation();
        this.isOpen = true;
    };

    private onPopoverClose = (event: CustomEvent) => {
        event.stopPropagation();
        this.isOpen = false;
    };
}
